.page-header__logo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    vertical-align: middle;
}

.page-header__logo-icon {
    width: 100%;
    height: 100%;
    fill:  none;
    stroke: #ffffff;
}

.page-header--nav-opened {
    background-image: none;
    .page-header__promo {
        display: none;
    }

    .page-header__logo {
        margin-left: auto;
        margin-right: auto;
        margin-top: 93px;
    }
}
.page-header__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    margin-left: 0;
    margin-right: 0;
}

.page-header__social {
    position: absolute;
    bottom: 59px;
    left: 5%;
}

@media (min-width: $min-desktop-width) {
    .page-header {
      min-height: 1044px;
      }

    .page-header__promo-gallery-span {
        display: none;
    }

    .page-header__promo-catalog-link {
        position: absolute;
        display: block;
        content: "";
        width: 221px;
        height: 22px;
        // padding-right: 31px;
        bottom: 218px;
        right: -2px;
        font-family: $font;
        font-weight: 400;
        font-size: 20px;
        line-height: 22px;
        text-align: left;
        transform: rotate(90deg);
        outline: 1px solid green;

        &::after {
            position: absolute;
            content: "";
            width: 19px;
            height: 2px;
            background-color: $white;
            top: 10px;
            right: 10px;
        }

        &::before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            border-right: 1px solid $white;
            border-bottom: 1px solid $white;
            top: 7px;
            right: 10px;
            transform: rotate(-45deg);
        }
    }

    .page-header__wrapper {
        min-height: 1044px;
        max-width: $max-desktop-width;
        margin-left: auto;
        margin-right: auto;
        padding-left: 4.7%;
        padding-right: 4.7%;
        padding-bottom: 59px;
    }

    .page-header__social-list {
        flex-direction: column;
        width: 25px;
        height: 172px;
    }

    .page-header--nav-opened {
        background-image: none;

        .page-header__logo {
            margin-left: auto;
            margin-right: auto;
            margin-top: 93px;
            width: 158px;
            height: 49px;
        }
    }

    .page-header--nav-closed {
        position: relative;
        background-image: url("../img/main-bg-desktop@1x.jpg");
        background-repeat: no-repeat;
        background-position: 0 50%;
        background-size: 100% auto;
    
        @media (min-resolution: $retina-dpi),
          (min-resolution: $retina-dppx) {
          background-image: url("../img/main-bg-desktop@2x.jpg");
          background-repeat: no-repeat;
          background-position: 0 50%;
          background-size: 100% auto;
        }

        &::after {
            position: absolute;
            content: "";
            display: block;
            height: 120px;
            width: 100%;
            right: 0;
            bottom: 0;
            background-color: $transparent;
            background-image: linear-gradient(to top, $background-color 0%, $transparent 100%);
        }

        .page-header__wrapper {
            justify-content: flex-start;
        }

        .page-header__social {
            bottom: 151px;
        }

        .page-header__promo {
            width: 820px;
            margin-left: auto;
            margin-right: auto;
            outline: 1px solid green;
        }

        .page-header__logo {
            margin-left: 5px;
            margin-top: 86px;
            width: 137px;
            height: 47px;
        }

        .page-header__promo-slogan {
            margin-top: 189px;
            margin-bottom: 0;
            font-family: $font;
            font-weight: 400;
            font-size: 120px;
            line-height: 132px;
            text-transform: uppercase;
            letter-spacing: calc(1em / 11);
        }
    }

}
